.sideDrawer {
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    top: 0;
    left: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform .3s ease-out;
  }
  
  @media (min-width:500px) {
    .sideDrawer {
      display: none;
    }
  }
  
  .open {
    transform: translateX(0);
  }
  
  .close {
    transform: translateX(-100%);
  }
  .logo {
    height: 11%;
    margin-bottom: 32px;
  }
